		},
		hide() {
			if (this.examshow == true) {
				console.log('hello world');
				this.examshow = false;
				this.downloaded = false;
			}
		},
		hideModalcard() {
			if (this.modalCard == true) {
				this.modalCard = false;
			}
		},
		getUserInfo() {
			wx.showLoading({
				title:"登陆中",
				mask:true
			})
			var that = this;
			wx.authorize({
				scope: 'scope.userInfo',

				complete(e) {
					uni.getSetting({
						success(res) {
							if (res.authSetting['scope.userInfo']) {
								wx.setStorageSync("eid",-1);
								that.onGetUserInfo();
							} else {
							    wx.hideLoading();
								wx.showToast({
									title: '授权失败',
									icon: 'none'
								});
							}
						}
					});
				}
			});
		},

		onGetUserInfo() {
			//const userInfo = event.detail.userInfo
			/*  wx.showLoading(); */
			var that = this;

			wx.login({
				success: function(login_res) {
					wx.getUserInfo({
						success: function(res) {
							/* 	console.log(2222222);
									console.log(login_res);
									console.log(res); */
							var userInfo = res.userInfo;
							wx.request({
								url: that.$server + '/api/me/login',
								method: 'POST',
								withCredentials: true,
								header: {
									'content-type': 'application/x-www-form-urlencoded'
								},
								data: {
									code: login_res.code,
									userHead: userInfo.avatarUrl,
									userName: userInfo.nickName,
									userGender: userInfo.gender,
									userCity: userInfo.city,
									userProvince: userInfo.province
								},
								success: function(res) {
									/* var userInfo = res.data.data;

											console.log(userInfo); */

									that.hasUserInfo = true;
									that.userInfo = userInfo;
									that.avatarUrl.push(userInfo.avatarUrl);
									that.subtitle="欢迎";

									wx.hideLoading();
									var eid= wx.getStorageSync("eid");
									
									if(isNaN(eid) || eid==-1){
									that.modalCard = true;
									}else{
										uni.showModal({
											title: '提示',
											content: "是否继续考试",
											success: function(res) {
												if (res.confirm) {
													uni.navigateTo({
														url:"/pages/exam/start?eid="+eid+"&statu=1",
													})
												} else if (res.cancel) {
													wx.setStorageSync("eid",-1);
													//console.log('用户点击取消')
												}
											}
										})
									}
									// 将返回的数据保存到全局的缓冲中，方便其他页面使用

									wx.setStorageSync('sessionid', res.data.data);
								}
							});
						},
						fail: function(res) {
							uni.hideLoading();
							that.modalCard = true;

							/* wx.showToast({
										"title":"请检查网络连接",
										"icon":"none"
									}) */
							/* console.log(444444444);
									console.log(res); */
						}
					});
				},
				fail: function(res) {
					uni.hideLoading();
					that.modalCard = true;
				}
			});
			/* this.setData({
			            hasUserInfo: true,
			            userInfo: userInfo
			        }) */
			//}
		},
		checkAuth() {
			uni.showLoading({ title: '加载中' });
			var that = this;

			uni.getSetting({
				success(res) {
					if (res.authSetting['scope.userInfo']) {
						that.subtitle = '欢迎';
						that.onGetUserInfo();
					} else {
						that.subtitle = '授权';

						uni.hideLoading();
						//that.modalCard=true;
					}
				},
				fail(res) {
					uni.hideLoading();
					//console.log(res);
				}
			});
		},
		chapter(i) {
			if (this.hasUserInfo == true) {
				uni.navigateTo({
					url: '/pages/chapter/index?pid=' + (i + 1)
				});
			} else {
				uni.showToast({
					icon: 'none',
					title: '请授权登录'
				});
			}
		},
		download(exam) {
			var that = this;
			if (that.downloaded == false) {
				var eid = exam.id;
				uni.showLoading({
					title: '正在下载'
				});
				uni.request({
					url: that.$server + '/exam/download?eid=' + eid,
					method: 'GET',
					dataType: 'json',
					success(res) {
						if (res.data.code == 200) {
							var url = that.$server + '/paper/' + res.data.data.path + '.pdf';
							console.log(url);
							//url="http://cn.createpdfonline.org/pdffiles/hello%20word(20200623144016).pdf";
							/* var filename = url.split('/');
							filename=filename[filename.length-1]; */

							var temp = url.split('/');
							var fileName = temp[temp.length - 1];
							wx.downloadFile({
								url: url,
								success: function(res) {
									const manage = wx.getFileSystemManager();
									if (res.statusCode === 200) {
										var tempFilePath = res.tempFilePath;
										var dir = new Date().getTime();
										manage.mkdir({
											dirPath: wx.env.USER_DATA_PATH + '/' + dir,
											recursive: false,
											success(res) {
												manage.saveFile({
													tempFilePath: tempFilePath,
													filePath: wx.env.USER_DATA_PATH + '/' + dir + '/' + fileName,
													success: function(res) {
														that.downloaded = true;
														uni.hideLoading();
														uni.showToast({
															icon: 'none',
															title: '下载成功'
														});
													},
													fail(res) {
														uni.hideLoading();
														uni.showToast({
															icon: 'none',
															title: '下载失败,请重试'
														});
													}
												});
											},
											fail(res) {
												uni.hideLoading();
												uni.showToast({
													icon: 'none',
													title: '下载失败,请重试'
												});
											}
										});
									} else {
										uni.hideLoading();
										uni.showToast({
											icon: 'none',
											title: '下载失败,请重试'
										});
									}
								}
							});

						} else {
							uni.hideLoading();
							uni.showToast({
								icon: 'none',
								title: '下载失败'
							});
						}
					},
					fail(res) {
						uni.hideLoading();
						console.log(res);
						uni.showToast({
							icon: 'none',
							title: '网络连接失败'
						});
					},
					complete() {}
				});
			} else {
				uni.showToast({
					icon: 'none',
					title: '下载成功'
				});
			}
		},
		operate(i) {
			if (this.examshow && this.paper != null) {
				if (i == 0) {
					wx.setStorageSync("eid",this.paper.id);
					uni.navigateTo({
						url: '/pages/exam/start?eid=' + this.paper.id
					});
				} else {
					this.download(this.paper);
				}
			} else {
				uni.showToast({
					icon: 'none',
					title: '出现错误了'
				});
			}
		},
		test() {
			uni.request({
				url: this.$server + '/user/set',
				success(res) {
					console.log(res);
				}
			});
		},
		menu(i) {
			var that = this;
			if (that.hasUserInfo == true) {
				switch (i) {
					case 0:
						Util.jump('/pages/user/history');
						break;
					case 1:
						this.to(0);
						break;
					case 2:
						this.to(1);
						break;
					case 3:
						this.to(2);
						break;
					case 4:
						Util.jump('/pages/exam/download');
						break;
				}
			} else {
				uni.showToast({
					icon: 'none',
					title: '请授权登陆'
				});
			}
		},
		to(type) {
			var that = this;
			var url = this.$server + '/question/get?type=' + type;
			uni.request({
				url: url,
				header: { Cookie: 'JSESSIONID=' + that.JSESSIONID() },
				withCredentials: true,
				success(res) {
					console.log(res.data);
					if (res.data.code == 200) {
						if (res.data.data != null) {
							Util.jump('/pages/user/questions?type=' + type + '&pn=0');
						}
					} else {
						uni.showToast({
							icon: 'none',
							title: '无数据'
						});
					}
				},
				fail(res) {
					uni.showToast({
						icon: 'none',
						title: '无数据'
					});
				}
			});
		},
		open(item) {
			item.visible = true;
		},
		submit() {
			this.$refs['confirm'].open({
				title: '提示',
				message: '确定添加该条数据吗？',
				callback: ({ action }) => {
					if (action === 'confirm') {
						this.visible = false;
					}
				}
			});
		},

		exam(i) {
			var that = this;
			if (i == 0 || i == 1) {
				if (that.hasUserInfo == true) {
					uni.request({
						url: this.$server + '/exam/get?type=' + (i + 1),
						method: 'GET',
						dataType: 'json',
						header: { Cookie: 'JSESSIONID=' + that.JSESSIONID() },
						success(res) {
							if (res.data.code == 200) {
								var eid = null;
								if (res.data.data != null) {
									eid = res.data.data.id;
								}

								if (eid != null) {
									if (i == 0) {
										that.paper = res.data.data;
										that.examshow = true;
										that.downloaded = false;
									} else {
										wx.setStorageSync("eid",eid);
										Util.jump('/pages/exam/start?eid=' + eid);
									}
								} else {
									uni.showToast({
										icon: 'none',
										title: '无数据'
									});
								}
							} else {
								uni.showToast({
									icon: 'none',
									title: '出现问题了'
								});
							}
						},
						fail(res) {
							uni.showToast({
								icon: 'none',
								title: '出现问题了'
							});
						},
						complete() {}
					});
				} else {
					uni.showToast({
						icon: 'none',
						title: '请授权登陆'
					});
				}
			} else if (i == 2) {
				if (that.hasUserInfo == true) {
					uni.request({
						url: this.$server + '/exam/zt',
						withCredentials: true,
						success(res) {
							if (res.data.code == 200) {
								uni.hideLoading();
								if (res.data.data != null && res.data.data.length > 0) {
									Util.jump('/pages/exam/zt');
								} else {
									uni.showToast({
										icon: 'none',
										title: '无数据'
									});
								}
							} else {
								uni.showToast({
									icon: 'none',
									title: '出现问题了'
								});
							}
						},
						fail(res) {
							uni.showToast({
								icon: 'none',
								title: '出现问题了'
							});
						}
					});
				} else {
					uni.showToast({
						icon: 'none',
						title: '请授权登陆'
					});
				}
			} else {
				/* Util.jump('/pages/knowledge/index'); */
				Util.jump('/pages/knowledge/index');
				//Util.jump('/pages/news/index');
			}
		}
	}
};
